<template>
  <div class="player" v-show="playList.length>0"> 
    <!-- 未选择 -->
    <transition name="normal" @enter="enter" @after-enter="afterEnter" @leave="leave" @after-leave="leaveEnter">
      <div class="normal-player" v-show="fullScreen">
        <div class="background">
          <img height="100%" width="100%" :src="currentSong.image">
        </div>
        <div class="top">
          <div class="back" @click="closeNormalPlayer">
            <i class="icon-back"></i>
          </div>
          <h1 class="title" v-html="currentSong.name"></h1>
          <h2 class="subtitle" v-html="currentSong.singer"></h2>
        </div>
        <div class="middle"
        @touchstart.prevent="middleTouchStart"
        @touchmove.prevent="middleTouchMove"
        @touchend="middleTouchEnd"
        >
          <div class="middle-l" ref="middle">
            <div class="cd-wrapper" ref="cdWrapper">
              <div class="cd" :class="cdCls">
                <img class="image" :src="currentSong.image">
              </div>
            </div>
            <div class="playing-lyric-wrapper">
              <div class="playing-lyric">{{ playingLyric }}</div>
            </div>
          </div>
          <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
            <div class="lyric-wrapper">
              <div v-if="currentLyric">
                <p ref="lyricLine"
                class="text"
                :class="{'current': currentLineNum === index}"
                v-for="(line,index) in currentLyric.lines">{{ line.txt }}</p>
              </div>
            </div>
          </scroll>
        </div>
        <div class="bottom">
          <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow==='cd'}"></span>
            <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
          </div>
          <div class="progress-wrapper">
            <span class="time time-l">{{ format(currentTime) }}</span>
            <div class="progress-bar-wrapper">
              <progress-bar :percent="playPercent" @precentChange="onProgressChange"></progress-bar>
            </div>
            <span class="time time-r">{{ format(currentSong.duration)}}</span>
          </div>
          <div class="operators">
            <div class="icon i-left">
              <i :class="iconMode" @click="changeMode"></i>
            </div>
            <div class="icon i-left" :class="disableCls">
              <i class="icon-prev" @click="togglePrev"></i>
            </div>
            <div class="icon i-center" :class="disableCls">
              <i :class="playIcon" @click="togglePlaying"></i>
            </div>
            <div class="icon i-right" :class="disableCls">
              <i class="icon-next" @click="toggleNext"></i>
            </div>
            <div class="icon i-right">
              <i class="icon" :class="favoriteCls" @click="toggleFavorite"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mini-fade">
      <div class="mini-player" v-show="!fullScreen" @click="openNormalPlayer">
        <div class="icon">
          <img width="40" height="40" :src="currentSong.image" :class="cdCls">
        </div>
        <div class="text">
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc" v-html="currentSong.singer"></p>
        </div>
        <div class="control">
          <progress-circle :radius="32" :percent="playPercent">
          <i :class="miniIcon" class="icon-mini" @click.stop="togglePlaying"></i>
          </progress-circle>
        </div>
        <div class="control">
          <i class="icon-playlist" @click.stop="showPlayList" ></i>
        </div>
      </div>
    </transition>
    <audio ref="audio" autoplay :src="currentSong.url" @canplay="ready" @error.stop="error" @timeupdate="updateTime" @ended="playEnd" ></audio>
    <transition name="alert">
      <alert v-if="isAlert" :text="alertText"></alert>
    </transition>
    <play-list :showFlag="isShowPlayList" ref="playlist" @changeMode="changeMode"></play-list>
  </div>
</template>

<script>
import { mapGetters, mapMutations, mapActions } from 'vuex'
import animations from 'create-keyframe-animation'
import Alert from 'base/alert/alert.vue'
import progressBar from 'base/progress-bar/progress-bar.vue'
import progressCircle from 'base/progress-circle/progress-circle.vue'
import { prefixStyle } from 'common/js/dom.js'
import Lyric from 'lyric-parser'
import Scroll from 'base/scroll/scroll.vue'
import { playMode } from 'common/js/config.js'
import { shuffle } from 'common/js/util.js'
import PlayList from 'components/playlist/playlist.vue'
import { playerMixin } from 'common/js/mixin.js'

const transform = prefixStyle('transform')
export default {
  mixins: [playerMixin],
  data: function() {
    return {
      alertText: '',
      isAlert: false,
      songReady: false,
      currentTime: 0,
      currentLyric: null,
      currentLineNum: 0,
      currentShow: 'cd',
      playingLyric: '',
      isShowPlayList: false
    }
  },
  components: {
    Alert,
    progressBar,
    progressCircle,
    Scroll,
    PlayList
  },
  created() {
    this.touch = {}
    // HACK PLAY
    // run Once
    document.querySelector('html').ontouchstart = () => {
      console.log('touch')
      if (this.songReady && window.ONCE_FLAG) {
        this.$refs.audio.play()
      }
    }
  },
  computed: {
    ...mapGetters(['fullScreen', 'playList', 'currentSong', 'playing', 'currentIndex', 'mode', 'sequenceList']),
    playIcon() {
      return this.playing ? 'icon-pause' : 'icon-play'
    },
    miniIcon() {
      return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
    },
    cdCls() {
      return this.playing ? 'play' : 'play pause'
    },
    disableCls() {
      return this.songReady ? '' : 'disable'
    },
    playPercent() {
      return this.currentTime / this.currentSong.duration
    },
    iconMode() {
      let cName = ''
      Object.keys(playMode).forEach(key => {
        if (playMode[key] === this.mode) {
          cName = `icon-${key}`
        }
      })
      return cName
    },
    favoriteCls() {
      return this.checkFavorite(this.currentSong) ? 'icon-favorite' : 'icon-not-favorite'
    }
  },
  methods: {
    ...mapActions(['savePlayHistory']),
    closeNormalPlayer() {
      this.setFullScreen(false)
    },
    openNormalPlayer() {
      this.setFullScreen(true)
    },
    error() {
      this.alertText = '歌曲获取失败 尝试下一首'
      this.isAlert = true
      this.songReady = true
      setTimeout(() => {
        this.isAlert = false
        this.toggleNext()
      }, 1200)
    },
    _alert(text) {
      this.alertText = text
      this.isAlert = true
      setTimeout(() => {
        this.isAlert = false
      }, 1200)
    },
    togglePlaying() {
      if (!this.songReady) return
      this.setPlayingState(!this.playing)
    },
    toggleNext() {
      if (!this.songReady) return
      if (this.playList.length === 1) {
        return this.loop()
      }
      this.setCurrentIndex(this.currentIndex >= this.playList.length - 1 ? 0 : this.currentIndex + 1)
      this.setPlayingState(true)
      this.songReady = false
    },
    playEnd() {
      if (this.mode === playMode.loop) {
        this.loop()
      } else {
        this.toggleNext()
      }
    },
    loop() {
      // 循环播放
      this.$refs.audio.currentTime = 0
      this.$refs.audio.play()
      this.currentLyric && this.currentLyric.seek(0)
    },
    togglePrev() {
      if (!this.songReady) return

      if (this.playList.length === 1) {
        this.songReady = true
        return this.loop()
      }
      this.setCurrentIndex(this.currentIndex <= 0 ? this.playList.length - 1 : this.currentIndex - 1)
      this.setPlayingState(true)
      this.songReady = false
    },
    onProgressChange(percent) {
      const currentTime = Number(percent * this.currentSong.duration)
      this.$refs.audio.currentTime = currentTime
      if (!this.playing) {
        this.togglePlaying()
      }
      this.currentLyric && this.currentLyric.seek(currentTime * 1000)
    },
    middleTouchStart(e) {
      // 避免从其他位置移动到此处触发move
      this.touch.initiated = true
      const touch = e.touches[0]
      this.touch.startX = touch.pageX
      this.touch.startY = touch.pageY
    },
    middleTouchMove(e) {
      if (!this.touch.initiated) {
        return
      }
      const opacity = this.currentShow === 'cd' ? 1 - this.touch.percent : this.touch.percent // 在不同页面下透明度应有不同的函数
      const touch = e.touches[0]
      const deltaX = touch.pageX - this.touch.startX
      const deltaY = touch.pageY - this.touch.startY
      if (Math.abs(deltaY) > Math.abs(deltaX)) return
      const left = this.currentShow === 'cd' ? 0 : -window.innerWidth // 计算歌词面板的left值
      this.touch.percent = Math.abs(deltaX / window.innerWidth)
      const width = Math.min(0, Math.max(-window.innerWidth, left + deltaX)) // 避免极限移出屏幕
      this.$refs.lyricList.$el.style['transform'] = `translate3d(${width}px,0,0)`
      this.$refs.middle.style['opacity'] = opacity
      this.$refs.lyricList.$el.style[`transition`] = 0
      this.$refs.middle.style['transition'] = 0
    },
    middleTouchEnd(e) {
      // 判断共四种情况下的最终偏移
      let width = 0
      let opacity = 1
      if (this.currentShow === 'cd') {
        if (this.touch.percent > 0.1) {
          // 避免percent不存在
          width = -window.innerWidth
          opacity = 0
          this.currentShow = 'lyric'
        } else {
          width = 0
          opacity = 1
        }
      } else if (this.currentShow === 'lyric') {
        if (this.touch.percent > 0.1) {
          width = 0
          opacity = 1
          this.currentShow = 'cd'
        } else {
          width = -window.innerWidth
          opacity = 0
        }
      }
      this.touch.initiated = false
      this.$refs.lyricList.$el.style[transform] = `translate3d(${width}px,0,0)`
      this.$refs.lyricList.$el.style[`transition`] = 'all 500ms'
      this.$refs.middle.style['transition'] = 'all 500ms'
      this.$refs.middle.style['opacity'] = opacity

      // clear

      this.touch.percent = 0
    },
    enter(el, done) {
      const { x, y, scale } = this._getPostAndScala()
      let animation = {
        0: {
          transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
        },
        60: {
          transfrom: `translate3d(0,0,0) scale(1.2)`
        },
        100: {
          transform: `translate3d(0,0,0) scale(1)`
        }
      }
      // 这是Norma player到Mini的动画 首先覆盖到小播放器专辑的位置 然后再回来 最后缩放正常
      animations.registerAnimation({
        name: 'move',
        animation,
        presets: {
          duration: 400,
          easing: 'linear'
        }
      })
      animations.runAnimation(this.$refs.cdWrapper, 'move', done)
    },
    toggleFavorite() {
      if (!this.checkFavorite(this.currentSong)) {
        this.saveFavorite(this.currentSong)
      } else {
        this.delFavorite(this.currentSong)
      }
    },
    updateTime(e) {
      this.currentTime = e.target.currentTime
    },
    format(interval) {
      interval = interval | 0
      const minute = (interval / 60) | 0
      const second = this._pad((interval % 60) | 0)
      return `${minute}:${second}`
    },
    changeMode() {
      const mode = (this.mode + 1) % 3
      this.setPlayMode(mode)
      this._alert(this.modeText)
      let list = null
      if (mode === playMode.random) {
        list = shuffle(this.sequenceList)
      } else {
        list = this.sequenceList
      }
      this.resetCurrentIndex(list)
      this.setPlayList(list)
    },
    resetCurrentIndex(list) {
      let index = list.findIndex(item => {
        return item.id === this.currentSong.id
      })
      this.setCurrentIndex(index)
    },
    _pad(num, n = 2) {
      let len = num.toString().length
      while (len < n) {
        num = '0' + num
        len++
      }
      return num
    },
    ready() {
      console.log('ready')
      this.songReady = true
      this.savePlayHistory(this.currentSong)
    },
    afterEnter(el, done) {
      animations.unregisterAnimation('move')
      this.$refs.cdWrapper.style.animation = ''
    },
    leave(el, done) {
      const { x, y, scale } = this._getPostAndScala()
      let animation = {
        0: {
          transform: `translate3d(0px,0px,0) scale(1)`
        },
        100: {
          transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
        }
      }
      animations.registerAnimation({
        name: 'back',
        animation,
        presets: {
          duration: 400,
          easing: 'linear'
        }
      })
      animations.runAnimation(this.$refs.cdWrapper, 'back', done)
    },
    leaveEnter(el, done) {
      animations.unregisterAnimation('back')
      this.$refs.cdWrapper.style.animation = ''
    },
    getLyric() {
      this.currentSong
        .getLyric()
        .then(
          lyric => {
            if (this.currentSong.lyric !== lyric) {
              // 歌曲变化 无须创建歌词
              return
            }
            this.currentLyric = new Lyric(lyric, this.handeLyric)
            if (this.playing) {
              this.currentLyric.play()
            }
          },
          err => {
            console.log(err)
          }
        )
        .catch(() => {
          this.currentLyric = null
          this.playingLyric = '歌词获取失败'
          this.currentLineNum = 0
        })
    },
    handeLyric({ lineNum, txt }) {
      this.currentLineNum = lineNum
      if (lineNum >= 5) {
        let lineEnm = this.$refs.lyricLine[lineNum - 5]
        this.$refs.lyricList.scrollToEle(lineEnm, 1000)
      } else {
        this.$refs.lyricList.scrollTo(0, 0, 1000)
      }
      this.playingLyric = txt
    },
    showPlayList() {
      this.$refs.playlist.show()
    },
    hidePlayList() {
      this.$refs.playlist.hide()
    },
    _getPostAndScala() {
      const targetWidth = 40
      const paddingLeft = 40 // 小专辑的中心点
      const paddingBottom = 30
      const paddingTop = 80
      const width = window.innerWidth * 0.8
      const scale = targetWidth / width
      const x = -(window.innerWidth / 2 - paddingLeft)
      const y = window.innerHeight - paddingTop - width / 2 - paddingBottom
      // 此处的xy 是 大专辑飞到小专辑的xy轴偏移
      return {
        x,
        y,
        scale
      }
    },
    resetLyric() {
      this.currentLyric && this.currentLyric.stop() // 清除计时器
      this.currentLineNum = -1 // 避免当前歌词停留
      this.playingLyric = ''
    },
    ...mapMutations({
      setFullScreen: 'SET_FULL_SCREEN',
      setPlayingState: 'SET_PLAYING_STATE',
      setCurrentIndex: 'SET_CURRENT_INDEX',
      setPlayMode: 'SET_PLAY_MODE',
      setPlayList: 'SET_PLAYLIST'
    })
  },
  watch: {
    currentSong(newSong, oldSong) {
      if (!newSong.id || newSong.id === oldSong.id) {
        return
      }
      this.resetLyric()
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        // HACK Play
        this.$refs.audio.play()
        this.getLyric()
      }, 1000)
    },
    playing(newPlaying) {
      const audio = this.$refs.audio
      this.currentLyric && this.currentLyric.togglePlay() // 切换歌词
      this.$nextTick(() => {
        newPlaying ? audio.play() : audio.pause()
      })
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';
@import '~common/stylus/mixin';

// animate
.normal-fade-enter-active, .normal-fade-leave-active {
  transition: all 0.4s;
}

.normal-fade-enter, .normal-fade-leave-to {
  opacity: 0;
  transform: translateY(50vh);
}

.mini-fade-enter-active, .mini-fade-leave-active {
  transition: all 0.4s;
}

.mini-fade-enter, .mini-fade-leave-to {
  opacity: 0;
}

.player {
  .normal-player {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 150;
    background: $color-background;

    .background {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      opacity: 0.6;
      filter: blur(40px);
    }

    .top {
      position: relative;
      margin-bottom: 25px;

      .back {
        position: absolute;
        top: 0;
        left: 6px;
        z-index: 50;

        .icon-back {
          display: block;
          padding: 9px;
          font-size: $font-size-large-x;
          color: $color-theme;
          transform: rotate(-90deg);
        }
      }

      .title {
        width: 70%;
        margin: 0 auto;
        line-height: 40px;
        text-align: center;
        no-wrap();
        font-size: $font-size-large;
        color: $color-text;
      }

      .subtitle {
        line-height: 20px;
        text-align: center;
        font-size: $font-size-medium;
        color: $color-text;
      }
    }

    .middle {
      position: fixed;
      width: 100%;
      top: 80px;
      bottom: 170px;
      white-space: nowrap;
      font-size: 0;

      .middle-l {
        display: inline-block;
        vertical-align: top;
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 80%;

        .cd-wrapper {
          position: absolute;
          left: 10%;
          top: 0;
          width: 80%;
          height: 100%;

          .cd {
            width: 100%;
            height: 100%;
            box-sizing: border-box;
            border: 10px solid rgba(255, 255, 255, 0.1);
            border-radius: 50%;

            &.play {
              animation: rotate 20s linear infinite;
            }

            &.pause {
              animation-play-state: paused;
            }

            .image {
              position: absolute;
              left: 0;
              top: 0;
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }
        }

        .playing-lyric-wrapper {
          width: 80%;
          margin: 30px auto 0 auto;
          overflow: hidden;
          text-align: center;

          .playing-lyric {
            height: 20px;
            line-height: 20px;
            font-size: $font-size-medium;
            color: $color-text-l;
          }
        }
      }

      .middle-r {
        display: inline-block;
        vertical-align: top;
        width: 100%;
        height: 100%;
        overflow: hidden;

        .lyric-wrapper {
          width: 80%;
          margin: 0 auto;
          overflow: hidden;
          text-align: center;

          .text {
            line-height: 32px;
            color: $color-text-l;
            font-size: $font-size-medium;

            &.current {
              color: $color-text;
            }
          }
        }
      }
    }

    .bottom {
      position: absolute;
      bottom: 50px;
      width: 100%;

      .dot-wrapper {
        text-align: center;
        font-size: 0;

        .dot {
          display: inline-block;
          vertical-align: middle;
          margin: 0 4px;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: $color-text-l;

          &.active {
            width: 20px;
            border-radius: 5px;
            background: $color-text-ll;
          }
        }
      }

      .progress-wrapper {
        display: flex;
        align-items: center;
        width: 80%;
        margin: 0px auto;
        padding: 10px 0;

        .time {
          color: $color-text;
          font-size: $font-size-small;
          flex: 0 0 30px;
          line-height: 30px;
          width: 30px;

          &.time-l {
            text-align: left;
          }

          &.time-r {
            text-align: right;
          }
        }

        .progress-bar-wrapper {
          flex: 1;
        }
      }

      .operators {
        display: flex;
        align-items: center;

        .icon {
          flex: 1;
          color: $color-theme;

          &.disable {
            color: $color-theme-d;
          }

          i {
            font-size: 30px;
          }
        }

        .i-left {
          text-align: right;
        }

        .i-center {
          padding: 0 20px;
          text-align: center;

          i {
            font-size: 40px;
          }
        }

        .i-right {
          text-align: left;
        }

        .icon-favorite {
          color: $color-sub-theme;
        }
      }
    }

    &.normal-enter-active, &.normal-leave-active {
      transition: all 0.4s;

      .top, .bottom {
        transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);
      }
    }

    &.normal-enter, &.normal-leave-to {
      opacity: 0;

      .top {
        transform: translate3d(0, -100px, 0);
      }

      .bottom {
        transform: translate3d(0, 100px, 0);
      }
    }
  }

  .mini-player {
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 180;
    width: 100%;
    height: 60px;
    background: $color-highlight-background;

    &.mini-enter-active, &.mini-leave-active {
      transition: all 0.4s;
    }

    &.mini-enter, &.mini-leave-to {
      opacity: 0;
    }

    .icon {
      flex: 0 0 40px;
      width: 40px;
      padding: 0 10px 0 20px;

      img {
        border-radius: 50%;

        &.play {
          animation: rotate 10s linear infinite;
        }

        &.pause {
          animation-play-state: paused;
        }
      }
    }

    .text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex: 1;
      line-height: 20px;
      overflow: hidden;

      .name {
        margin-bottom: 2px;
        no-wrap();
        font-size: $font-size-medium;
        color: $color-text;
      }

      .desc {
        no-wrap();
        font-size: $font-size-small;
        color: $color-text-d;
      }
    }

    .control {
      flex: 0 0 30px;
      width: 30px;
      padding: 0 10px;

      .icon-play-mini, .icon-pause-mini, .icon-playlist {
        font-size: 30px;
        color: $color-theme-d;
      }

      .icon-mini {
        font-size: 32px;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.alert-enter, .alert-leave-to {
  opacity: 0;
}

.alert-enter-active, .alert-leave-active {
  transition: all 0.4s;
}
</style>


